}

.swal2-popup {
  font-family: "Montserrat", sans-serif;
}
.swal2-popup h1, .swal2-popup h2, .swal2-popup h3 {
  font-size: 3rem !important;
}
.swal2-popup p, .swal2-popup div, .swal2-popup button {
  font-size: 1.6rem !important;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #333333;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #333333;
  font-size: 2rem;
}

.bg-menu {
  background-color: #f2c641;
}

.nav__menu {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  list-style-type: none;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .nav__menu {
    margin: 0;
    padding: 2rem 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

@media (max-width: 320px) {
  .nav__menu {
    flex-direction: column;
    gap: 0.4rem;
  }
}
.item__link {
  text-decoration: none;
  color: #333333;
}
.item__link:hover {
  color: #cccccc;
  color: #FFFFFF;
  cursor: pointer;
}

.menu__inicio {
  width: 45px;
  height: 45px;
}

.menu__link {
  text-decoration: none;
}

.banner__texto h1 {
  margin-top: 3rem;
}

input,
button {
  border: none;
  outline: none;
}

/*=============== LOGIN ===============*/
.login {
  position: relative;
  display: grid;
  align-items: center;
  margin-top: 3rem;
  margin: 4rem auto 2rem auto;
}
.login__img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.login__form {
  position: relative;
  background-color: #2d3133;
  border: 2px solid hsl(0, 0%, 100%);
  margin-inline: 2.4rem;
  padding: 4rem 2.4rem;
  border-radius: 1.6rem;
  backdrop-filter: blur(8px);
  margin: 0 auto;
}
.login__title {
  text-align: center;
  font-size: 2.8rem;
  font-weight: 500;
  margin-bottom: 3.2rem;
  color: hsl(0, 0%, 100%);
}
.login__content, .login__box {
  display: grid;
}
.login__content {
  row-gap: 2.8rem;
  margin-bottom: 2.4rem;
}
.login__box {
  grid-template-columns: max-content 1fr;
  align-items: center;
  column-gap: 1.2rem;
  border-bottom: 2px solid hsl(0, 0%, 100%);
}
.login__icon, .login__eye {
  font-size: 2rem;
}
.login__input {
  width: 100%;
  padding-block: 1.28rem;
  background: none;
  color: hsl(0, 0%, 100%);
  position: relative;
  z-index: 1;
}
.login__box-input {
  position: relative;
}
.login__label {
  position: absolute;
  left: 0;
  top: 13px;
  font-weight: 500;
  transition: top 0.3s, font-size 0.3s;
}
.login__eye {
  position: absolute;
  right: 0;
  top: 18px;
  z-index: 10;
  cursor: pointer;
}
.login__box:nth-child(2) input {
  padding-right: 2.88rem;
}
.login__check, .login__check-group {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.login__check {
  margin-bottom: 2.4rem;
}
.login__check-label, .login__forgot, .login__register {
  font-size: 1.3rem;
}
.login__check-group {
  column-gap: 0.8rem;
}
.login__check-input {
  width: 16px;
  height: 16px;
}
.login__forgot {
  color: hsl(0, 0%, 100%);
}
.login__forgot:hover {
  text-decoration: underline;
}
.login__button {
  width: 100%;
  padding: 1.6rem;
  border-radius: 0.8rem;
  background-color: hsl(0, 0%, 100%);
  font-weight: 500;
  cursor: pointer;
  margin-bottom: 3.2rem;
  color: black;
}
.login__register {
  text-align: center;
  color: hsl(0, 0%, 100%);
}
.login__register a {
  color: #f2c641;
  font-weight: 500;
}
.login__register a:hover {
  text-decoration: underline;
}

/* Input focus move up label */
.login__input:focus + .login__label {
  top: -12px;
  font-size: 1.3rem;
}

/* Input focus sticky top label */
.login__input:not(:placeholder-shown).login__input:not(:focus) + .login__label {
  top: -12px;
  font-size: 1.3rem;
}

@media screen and (min-width: 576px) {
  .login {
    justify-content: center;
  }
  .login__form {
    width: 432px;
    padding: 5rem 4.8rem 5rem;
    border-radius: 2.4rem;
  }
  .login__title {
    font-size: 3.2rem;
  }
}
.forget {
  position: relative;
  display: grid;
  align-items: center;
  margin: 1rem 0;
}
.forget .clave {
  margin-bottom: 2rem;
}

.acciones {
  display: flex;
  flex-direction: column;
}

:root {
  --color-1: #F2D541;
  --color-2: #F2C641;
  --color-3: #F2F2F2;
  --color-4: #000000;
  --fuente: "Montserrat", sans-serif;
}

.banner {
  background-image: url(../img/apiario3.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%; /* La imagen de fondo se ajustará al 100% del ancho y alto del contenedor */
  height: 250px; /* Ajusta la altura del contenedor según sea necesario */
  position: relative;
  margin-bottom: 3.2rem;
  margin-top: 0rem;
  width: auto;
}

.contenido-banner {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
