}

.contenedor-contactanos {
  width: auto;
  margin-top: 10px;
  margin-right: 3.2rem;
  margin-bottom: 0;
  margin-left: 3.2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .contenedor-contactanos {
    display: flex;
    flex-direction: row;
  }
}

.sombra {
  -webkit-box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.75);
  -moz-box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.75);
  box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.75);
  background-color: white;
  padding: 3.2rem;
  border-radius: 2.56rem;
}

.formulario-contacto {
  border: 1px solid #CED5D7;
  border-radius: 6px;
  padding: 45px 45px 20px;
  margin-top: 50px;
  background-color: white;
  box-shadow: 0px 5px 10px #B5C1C5, 0 0 0 10px #EEF5F7 inset;
}
@media (min-width: 768px) {
  .formulario-contacto {
    width: auto;
  }
}
@media (min-width: 1024px) {
  .formulario-contacto {
    width: 500px;
  }
}

.formulario-contacto label {
  display: block;
  font-weight: bold;
  color: #000000;
}

.formulario-contacto div {
  margin-bottom: 15px;
}

.campo-contacto input {
  padding: 7px 6px;
  width: auto;
  border: 1px solid #CED5D7;
